<template lang="pug">
  #panel(:style="panelStyle")
</template>

<script>
import EPaper from '../../common/epaper.js'
import {
  SetAgreementSign
} from '../../common/resources.js'
import {
  mapGetters,
  mapMutations
} from 'vuex'
import {
  RET_CODE_MAP,
  CUST_STATE_CODE_MAP
} from '../../constants.js'
import borrowMixins from './borrow_mixins.js'

export default {
  mixins: [borrowMixins],
  mounted() {
    const myepaper = EPaper.init('panel', {
      name: this.user.UserinfoValLogin.Name
    })

    myepaper.setCallback((pngData, pointsData, isCancel) => {
      if (!isCancel) {
        if (!pngData) {
          this.$root.toast(`${this.user.UserinfoValLogin.Name}`, 'error')
          return
        }

        SetAgreementSign
          .save({
            sign: pngData
          })
          .then(res => res.json())
          .then(data => {
            if (data.ret === RET_CODE_MAP.OK) {
              this.drawMoney()
            }
          })
      } else {
        this.updateStateCode(CUST_STATE_CODE_MAP.DEBT_SETTELED)
        this.$router.push({
          name: 'borrowerInfo'
        })

        // this.$root.toast('', 'error')
      }
    })
  },

  methods: {
    ...mapMutations(['updateStateCode'])
  },

  computed: {
    ...mapGetters(['user'])
  },

  data() {
    return {
      panelStyle: {
        height: window.innerHeight + 'px' // panelepapercanvas
      }
    }
  }
}
</script>

<style lang="scss">
@import '../../assets/scss/epaper.scss';
#panel {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  z-index: 0;
  background-color: #fff;
  .ePaperCanvas {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    display: block;
  }
}
</style>



